body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  text-align: left;
}
@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media (prefers-reduced-motion: no-preference) {
  .logo-spin {
    animation: logo-spin infinite 20s linear;
  }
}
.card {
  padding: 2em;
}
.read-the-docs {
  color: #888;
}
@media (max-width: 768px) {
  .header-selector {
    background-image: url('/img/web/helmet.png') !important;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 220px !important;
    width: 100%;
    display: block;
    position: relative;
  }
  .theme-toggle-btn {
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 50;
  }
}
@media (max-width: 640px) {
  .bento-card-content {
    height: auto !important;
    min-height: 200px;
  }
}
